import { registerApplication, start } from 'single-spa' //single-spa
import axios from 'axios'
import AppConfig from './appConfig'

/**
 * @name js
 * @description promisescript
 * @param {*} url
 * @returns
 */
const runScript = async (url: string) => {
  return new Promise((resolve, reject) => {
    const script = document.createElement('script')
    script.src = url
    script.onload = (res) => {
      resolve(res)
    }
    script.onerror = (err) => {
      console.log(err)
      reject()
    }
    const firstScript: HTMLElement = document.getElementsByTagName('script')[0]
    firstScript?.parentNode?.insertBefore(script, firstScript)
  })
}

const isObject = (obj: object) => {
  return Object.prototype.toString.call(obj) === '[object Object]'
}

/**
 * 
 * @param {*} host
 * @param {*} globalVar
 * @returns
 */
const loadApp = (host: string, globalVar: string, bundle: string) => {
  return async () => {
    await getManifest(`${host}/asset-manifest.json`, bundle, host)
    return (window as any)[globalVar]
  }
}

/**
 * @description 
 * @param {*} url stats-webpack-pluginwebpack-manifest-pluginmanifest
 * @param {*} bundle
 * @param {*} host host+port
 */
const getManifest = async (url: string, bundle: string, host: string) => {
  const { data } = await axios.get(url)
  const { entrypoints } = data
  let assets = []
  if (Array.isArray(entrypoints)) {
    assets = entrypoints
  } else {
    assets = entrypoints[bundle].assets
    assets = assets.map((obj: any) => {
      if (isObject(obj)) {
        return obj.name
      }
      return obj
    })
  }

  for (let i = 0; i < assets.length; i++) {
    await runScript(`${host}/${assets[i]}`)
  }
}

AppConfig.forEach((app) => {
  // 
  registerApplication({
    name: app.projectName,
    app: loadApp(app.host, app.projectName, app.bundle), // umdwindow
    activeWhen: app.activeWhen, // url
    customProps: app.customProps
  })
})

start() // 
